h1 {
    font-family: 'Redaction', serif, system-ui;
    text-align: center;
    color: rgb(255, 255, 255);
    font-size: 56px;
    padding-top: 180px;
}

h2 {
    font-family: 'Redaction', serif, system-ui;
    text-align: center;
    color: rgb(255, 255, 255);
    font-size: 107px;
    margin-top: -30px;
}

btn-primary {
    background-color: #ffffff;
    border-color: #ffffff;
    color: #fff;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    cursor: pointer;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    border: 1px solid transparent;
    margin-top: 20px;
}